@import url(https://fonts.googleapis.com/css?family=Roboto:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic&display=swap);
:root {
  --container-width: 1256px;
  --container-padding: 58px;
  --container-padding-mobile: 16px;
  --font-family: "Roboto", sans-serif;
  --page-bg: #fff;
  --header-color: #000;
  --accent: #f07c29;
  --steel-blue: #696f89;
  --gray:#aaa;
  --light-gray: #d3d7df;
  --text-color:#212121;
  --laptop-size: 1199px;
  --tablet-size: 959px;
  --mobile-size: 599px;
}

.dark {
  --page-bg: #252526;
  --text-color: #fff;
}

.none {
  display: none !important;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-scroll {
  overflow-y: hidden;
}

/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  color: #212121;
  font-family: "Roboto", sans-serif;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  width: 100%;
}

@media (max-width: 1024px) {
  .container {
    padding: 0;
  }
}
.books {
  margin-top: 50px;
  margin-bottom: 50px;
}
.books__wrapper {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: repeat(6, 1fr);
  grid-template-areas: "a a a b b b" "a a a c c c" "a a a d d d" "e e f f g g";
}
.books__wrapper:active, .books__wrapper:focus {
  border: 1px solid var(--accent);
}
.books__more {
  width: fit-content;
  height: 40px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 14px;
  line-height: 114%;
  letter-spacing: 0.05em;
  margin: 40px auto 0;
  transition: all 0.3s ease;
}
.books__more:hover.btn {
  color: var(--accent);
  cursor: pointer;
  border: 1px solid var(--accent);
}
.books .btn {
  display: block;
  border: 1px solid var(--gray);
  border-radius: 16px;
  padding: 12px 32px;
  text-transform: uppercase;
  text-align: center;
  color: var(--text-color);
  transition: all 0.3s ease;
}
.books .books__shuffle {
  background-color: var(--accent);
  color: #fff;
  position: fixed;
  top: 20px;
  right: 20px;
  border: none;
}

@media (max-width: 1024px) {
  .books {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .books__wrapper {
    display: flex;
    gap: 16px;
    overflow-x: auto;
    padding-left: 16px;
    padding-bottom: 20px;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: thin;
  }
  .books__more.btn {
    display: none;
  }
  .books .books__shuffle {
    display: none;
  }
}
