@mixin mediaBg() {

    @media (-webkit-min-device-pixel-ratio:2),
    (min-resolution:192dpi) {
        @content;
    }
}


@mixin tablet () {
    @media (max-width: 1220px) {
        @content;
    }
}

@mixin tabletLarge () {
    @media (max-width: 992px) {
        @content;
    }
}

@mixin mobile () {
    @media (max-width:820px) {
        @content;
    }
}